import fs from 'fs';
import path from 'path';

const DB_PATH = path.join(process.cwd(), 'data', 'bookings.json');

export interface Booking {
    id: string;
    userId: string;
    studioId: string;
    roomName?: string; // Added for detail
    date: string;       // YYYY-MM-DD
    startTime: string;  // HH:mm
    durationHours: number;
    userCount: number;
    totalPrice: number;
    createdAt: string;
}

// Initialize DB file if not exists
if (!fs.existsSync(DB_PATH)) {
    fs.writeFileSync(DB_PATH, JSON.stringify([], null, 2));
}

export const getBookings = (): Booking[] => {
    try {
        const data = fs.readFileSync(DB_PATH, 'utf-8');
        return JSON.parse(data);
    } catch (error) {
        return [];
    }
};

export const saveBooking = (booking: Booking): void => {
    const bookings = getBookings();
    bookings.push(booking);
    fs.writeFileSync(DB_PATH, JSON.stringify(bookings, null, 2));
};

// Check for overlaps
export const checkAvailability = (date: string, startTime: string, duration: number): boolean => {
    const bookings = getBookings();

    // Convert new request to minutes from midnight
    const [startH, startM] = startTime.split(':').map(Number);
    const newStart = startH * 60 + startM;
    const newEnd = newStart + (duration * 60);

    // Filter bookings for the same day
    const sameDayBookings = bookings.filter(b => b.date === date);

    for (const b of sameDayBookings) {
        const [h, m] = b.startTime.split(':').map(Number);
        const bStart = h * 60 + m;
        const bEnd = bStart + (b.durationHours * 60);

        // Overlap logic: (StartA < EndB) and (EndA > StartB)
        if (newStart < bEnd && newEnd > bStart) {
            return false; // Overlap found
        }
    }
    return true; // No overlap
};
